import React, { StrictMode, useEffect, useState } from "react";
import { loadStdlib } from '@reach-sh/stdlib'
import MyAlgoConnect from '@reach-sh/stdlib/ALGO_MyAlgoConnect';
import {
  Switch,
  Route,
  useHistory,
  useLocation
} from "react-router-dom";
import Home from './pages/Home'
import Sell from './pages/Sell'
import Auction from './pages/Auction'
import Navbar from './components/Navbar'
import Container from 'react-bootstrap/Container'
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DateAdapter from '@mui/lab/AdapterMoment';
import './App.css';
//import useGaTracker from './hooks/useGaTracker'
import queryString from 'query-string';

const APP_ID_BANK = 27856254
const ASSET_ID_JAM = 27735901
const approvalProgram = ''
const axios = require('axios');
const { REACT_APP_NETWORK_PROVIDER, REACT_APP_NETWORK } = process.env
const stdlib = loadStdlib(REACT_APP_NETWORK)
if (REACT_APP_NETWORK === 'ALGO') {
  stdlib.setWalletFallback(stdlib.walletFallback({
    providerEnv: "TestNet", MyAlgoConnect
  }));
}

const Faq = () => <div>
  Under Construction ...</div>

function App(props) {
  let history = useHistory()
  let location = useLocation()
  const { url } = queryString.parse(location?.search)
  if (url) {
    let [path, id] = url.split('/').slice(1)
    switch (path) {
      case 'faq':
        history.push(`/${path}`)
        break
      case 'sell':
      case 'auction':
        if (!isNaN(parseInt(id))) {
          history.push(`/${path}/${id}`)
        } else {
          history.push(`/${path}`)
        }
        break
      default:
        break
    }
  }
  //useGaTracker()
  const [state, setState] = useState({
    APP_ID_BANK,
    ASSET_ID_JAM,
    once: false,
    acc: null,
    stdlib
  })
  const connectAccountUsingLocalStorage = async () => {
    let acc = null
    try {
      let addr = localStorage.getItem('addr')
      acc = await stdlib.connectAccount({ addr })
    } catch (e) {
      console.log({ e })
    }
    return acc
  }
  const connectAccountUsingDefaultAccount = async () => {
    let acc = null
    try {
      acc = await stdlib.getDefaultAccount()
      localStorage.setItem('addr', acc.networkAccount.addr)
    } catch (e) {
      console.log({ e })
    }
    return acc
  }
  const connectAccount = async (method) => {
    console.log("Getting account ...")
    let acc
    switch (method) {
      case 'LocalStorage':
        acc = await connectAccountUsingLocalStorage()
        break
      default:
        acc = await connectAccountUsingDefaultAccount()
    }
    if (!acc) return
    let accInfo = (await axios.get(`https://testnet.algoexplorerapi.io/v2/accounts/${acc?.networkAccount?.addr}`))?.data
    let assetInfo = (await Promise.all([
      ...accInfo?.assets?.filter(el => el.amount > 0)?.map(el =>
        axios.get(`https://testnet.algoexplorerapi.io/idx2/v2/assets/${el["asset-id"]}`))
    ])).map(el => ({ ...el, ...(el?.data?.asset) }))
    let appInfo = accInfo['created-apps'].filter(el => el?.params['approval-program'] === approvalProgram)
    return {
      ...acc,
      ...accInfo,
      assetInfo,
      appInfo
    }
  }
  const disconnectAccount = () => {
    localStorage.removeItem('addr')
    history.go('/')
    //setState({...state, acc: null})
  }
  useEffect(() => {
    if (!state.once) {
      (async () => {
        setState({
          ...state,
          once: true,
          acc: await connectAccount('LocalStorage')
        })
      })();
    }
  })
  const handleConnect = async () => {
    let acc = await connectAccount()
    setState({ ...state, acc })
  }
  const containerStyle =
  {
    "alignItems": "baseline",
    "display": "flex",
    "justifyContent": "center",
    "height": "100%",
  }
  const Banner = () => {
    const stream = Array.from({ "length": 20 }).map(() => "//////// You are using the TestNet version").join(" ")
    const { initialSeconds = 0 } = props
    const [seconds, setSeconds] = useState(initialSeconds)
    useEffect(() => {
      let myInterval = setInterval(() => {
        if (seconds < stream.length / 2) {
          setSeconds(seconds + 1)
        } else {
          setSeconds(initialSeconds)
        }
      }, 1000 + seconds * seconds)
      return () => {
        clearInterval(myInterval)
      };
    });
    return (
      <div style={{ "background": "black", "color": "green", "overflow": "hidden", "height": "30px", "lineHeight": "30px" }} >
        {stream.substr(seconds)}
      </div>
    )
  }
  return (
    <LocalizationProvider dateAdapter={DateAdapter}>
      <div className="App">
        <Navbar 
          {...state}
          handleConnect={handleConnect}
          handleDisconnect={disconnectAccount} />
        <Container
          style={containerStyle}
          size="lg"
          fluid="lg">
          <Switch>
            <Route exact path="/auction">
              <Auction {...state} />
            </Route>
            <Route path="/auction/:appId">
              <Auction {...state} />
            </Route>
            <Route exact path="/sell">
              <Sell {...state} />
            </Route>
            <Route path="/sell/:appId">
              <Sell {...state} />
            </Route>
            <Route exact path="/faq">
              <Faq />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </Container>
      </div>
    </LocalizationProvider>
  );
}

export default App;